import React, { Component } from 'react';
import CSSModules from 'react-css-modules';
import styles from './styles.scss';

import { connect } from 'react-redux';
import { bindingActionCreators } from 'react-redux';

import StandardButton from '../../components/StandardButton';
import ChangerTab from './components/ChangerTab';
import Tab from './components/Tab';
import CounterTab from './components/CounterTab';

import { service } from '../../services/Carrer/service.js';
import { carrerActions } from  '../../services/Carrer/actions';

import Button from './components/Button';

class Carrer extends Component{

    getButtons(tab){
        let buttons = [];

        buttons.push({ type: 'localizationData', label: 'Lokalizacja' });
        buttons.push({ type: 'universityData', label: 'Uczelnia' });

        return buttons;
    }

    render(){

        const { 
            onClickShowButtonObjects, 
            onClickBackButtonObjects,
            onClickNextButtonObjects,
            onClickSetNextTab,
            onClickSetPreviousTab,
            onClickCreateTab
        } = this.props;
        debugger;
        return(
            <div styleName="carrer">
                <div styleName="header-buttons">
                    <StandardButton label="Nowa karta" onClick={() => onClickCreateTab()} />
                </div>
        
                <div>
                    <CounterTab />
                </div>
                <div styleName="tab-container">
                    <ChangerTab type="left" onClick={() => onClickSetPreviousTab()} />
                    <Tab>
                        <Button type="localizationData" label="Lokalizacja" />
                        <Button type="universityData" label="Uczelnia" />
                    </Tab>
                    <ChangerTab type="right" onClick={() => onClickSetNextTab()} />
                
                </div>
            </div>

        )
    }
}

const mapStateToProps = (state, ownProps) => {

}

const mapDispatchToProps = dispatch => {
    return {
        onClickCreateTab: () => {
            dispatch(carrerActions.createTab());
        },
        onClickSetPreviousTab: () => {
            dispatch(carrerActions.setPreviousTab());
        },
        onClickSetNextTab: () => {
            dispatch(carrerActions.setNextTab());
        }
    }
}

const CarrerComponent = CSSModules(Carrer, styles); 

export default connect(mapStateToProps, mapDispatchToProps)(CarrerComponent);


