import update from 'immutability-helper';

export const service = {
    createTab,
    setNextTab,
    setPreviousTab,
    loadButtonObjects,
    backButtonObjects,
    getButtonObjects,
    getActiveTab,
    showButtonObjects
}

// public methods

function createTab(tabs){
    const activeTabIndex = getActiveTabIndex(tabs);
    const newTabId = tabs.length + 1;

    return update(tabs, {
        [activeTabIndex]: {
            selected: {$set: false}
        },
        $push: [{ id: newTabId, selected: true, localizationData: {selectedIdLevelMap: [], values: [{ id: 1, parentId: null, level: 0, name: 'Dolnośląskie' }, 
        { id: 2, parentId: null, level: 0, name: 'Łódzkie' },
        { id: 1, parentId: 1, level: 1, name: 'Wrocław' }, 
        { id: 2, parentId: 2, level: 1, name: 'Wieluń' }]}, universityData: {selectedIdLevelMap: [], values: [{ id: 1, parentId: null, level: 0, name: 'Techniczna' }, 
        { id: 2, parentId: null, level: 0, name: 'Medyczna' },
        { id: 1, parentId: 1, level: 1, name: 'PWr' }, 
        { id: 2, parentId: 2, level: 1, name: 'PCZ' } ]} }]
    })
}

function setNextTab(tabs){
    const selectedTab = getActiveTab(tabs);
    return setActiveTabById(tabs, selectedTab.id + 1);
}


function setPreviousTab(tabs){
    const selectedTab = getActiveTab(tabs);
    return setActiveTabById(tabs, selectedTab.id - 1);
}

function loadButtonObjects(tabs, selectedId, level, type){
    const activeTab = getActiveTab(tabs);
    const activeIndexTab = getActiveTabIndex(tabs);
    const maxLevel = getMaxLevel(activeTab[type].values);

    if(level > maxLevel){
        return tabs;
    }

    return update(tabs, {
            [activeIndexTab]: {
                [type]: {
                    selectedIdLevelMap: {$push: [{selectedId, level}]},
                    }
                }
            });
}

function backButtonObjects(tabs, type){
    const activeTab = getActiveTab(tabs);
    const activeIndexTab = getActiveTabIndex(tabs);
    const lastIndexOfSelectedMap = activeTab[type].selectedIdLevelMap.length - 1;
    if(lastIndexOfSelectedMap === 0){
        return tabs;
    }
    
    return update(tabs, {
        [activeIndexTab]: {
            [type]: {
                selectedIdLevelMap: {$splice: [[lastIndexOfSelectedMap, 1]]}
            }
        }
    })
}

function getButtonObjects(data){
    const totalCountOfSelectedMaps = data.selectedIdLevelMap.length;

    if(totalCountOfSelectedMaps === 0){
        return [];
    }

    const {selectedId, level} = data.selectedIdLevelMap[totalCountOfSelectedMaps - 1];

    return data.values.filter(b => b.parentId === selectedId && b.level === level);
}

function showButtonObjects(tabs, type){
    const activeIndexTab = getActiveTabIndex(tabs);
    
    return update(tabs, {
        [activeIndexTab]: {
            [type]:{
                isOpen: {$set: !tabs[activeIndexTab][type].isOpen}
            }
        }
    })
}

function getActiveTab(tabs){
    return tabs.filter(t => t.selected === true)[0];
}

// private methods

function getActiveTabIndex(tabs){
    return tabs.findIndex(b => b.selected === true);
}

function setActiveTabById(tabs, id){
    const maxTabId = tabs.length;

    if(id === 0 || id > maxTabId){
        return tabs;
    }

    const activeIndexTab = getActiveTabIndex(tabs);
    const newIndexTab = tabs.findIndex(b => b.id === id);
    const updatedTabs = update(tabs,{
        [newIndexTab]:{
            selected: {$set: true}
        },
        [activeIndexTab]: {
            selected: {$set: false}
        }
    })

    return updatedTabs;
}

function getMaxLevel(map){
    return map.reduce((prev, current) => (prev.level > current.level) ? prev.level : current.level)
}


