import update from 'immutability-helper';

import CarrerActionTypes from './actionTypes';

import { service } from './service';

export const carrerActions = {
    createTab,
    setNextTab,
    setPreviousTab,
    loadButtonObjects,
    backButtonObjects,
    showButtonObjects
}

function createTab() {
    return (dispatch, getState) => {
        const state = getState().carrer;
        const tabs = service.createTab(state.tabs)

        dispatch({ type: CarrerActionTypes.GENERIC_UPDATE_TABS, tabs });
    }
}

function setNextTab() {
    return (dispatch, getState) => {
        const state = getState().carrer;
        const tabs = service.setNextTab(state.tabs);

        dispatch({ type: CarrerActionTypes.GENERIC_UPDATE_TABS, tabs });
    }
}

function setPreviousTab() {
    return (dispatch, getState) => {
        const state = getState().carrer;
        const tabs = service.setPreviousTab(state.tabs);

        dispatch({ type: CarrerActionTypes.GENERIC_UPDATE_TABS, tabs });
    }
}

function showButtonObjects(type) {
    return (dispatch, getState) => {
        const state = getState().carrer;
        let tabs = service.showButtonObjects(state.tabs, type);
        const activeTab = service.getActiveTab(state.tabs);
        if(activeTab[type].selectedIdLevelMap.length === 0){
            tabs = service.loadButtonObjects(tabs, null, 0, type);
        }

        dispatch({ type: CarrerActionTypes.GENERIC_UPDATE_TABS, tabs });
    }
}

function loadButtonObjects(selectedId, level, type) {
    return (dispatch, getState) => {
        dispatch(request());
        const state = getState().carrer;
        const tabs = service.loadButtonObjects(state.tabs, selectedId, level, type);

        dispatch({ type: CarrerActionTypes.GENERIC_UPDATE_TABS, tabs });
    }

    function request() { return { type: CarrerActionTypes.REQUEST } }
}

function backButtonObjects(type) {
    return (dispatch, getState) => {
        const state = getState().carrer;
        const tabs = service.backButtonObjects(state.tabs, type);
        
        dispatch({ type: CarrerActionTypes.GENERIC_UPDATE_TABS, tabs });
    }
}


