import CarrerActionTypes from './actionTypes';
import { service } from './service';

const defaultState = {
    tabs: [
        {
            id: 1,
            selected: true,
            localizationData: {
                isOpen: false,
                selectedIdLevelMap: [],
                values: [ 
                    { id: 1, parentId: null, level: 0, name: 'Dolnośląskie' }, 
                    { id: 2, parentId: null, level: 0, name: 'Łódzkie' },
                    { id: 1, parentId: 1, level: 1, name: 'Wrocław' }, 
                    { id: 2, parentId: 2, level: 1, name: 'Wieluń' } 
                ]
            },
            universityData: {
                isOpen: false,
                selectedIdLevelMap: [],
                values: [ 
                    { id: 1, parentId: null, level: 0, name: 'Techniczna' }, 
                    { id: 2, parentId: null, level: 0, name: 'Medyczna' },
                    { id: 1, parentId: 1, level: 1, name: 'PWr' }, 
                    { id: 2, parentId: 2, level: 1, name: 'PCZ' } 
                ]
            }
        }
    ] 
}

export default function carrer(state = defaultState, action = {}){
    switch(action.type){
        case CarrerActionTypes.GENERIC_UPDATE_TABS:
            const tabs = action.tabs;
            
            return {...state, tabs};

        case CarrerActionTypes.REQUEST: //todo
            return {...state};

        default: return state;
    }
}


