import React, { Component } from 'react';
import CSSModules from 'react-css-modules';
import styles from './styles.scss';

import { connect } from 'react-redux';

import {service} from '../../../../services/Carrer/service';

import ObjectList from './components/ObjectList';

import { carrerActions } from  '../../../../services/Carrer/actions';

class Button extends Component{
    constructor(props){
        super(props);
    }

    render(){
        debugger;
        const {buttonData} = this.props;

        const values = service.getButtonObjects(buttonData);
        return(
            <div styleName="ButtonContainer">
                <button onClick={this.props.onClickShowValues} styleName="carrer-button">{this.props.label}</button>
                
                <a style={getVisibility(buttonData.isOpen)} onClick={() => this.props.onClickBackButtonValues()}><i className='fas fa-arrow-circle-left'></i></a>
                <div style={getDisplay(buttonData.isOpen)}>
                    <ObjectList objects={values} onClick={(v) => this.props.onClickLoadValuesByParentId(v.id, v.level, this.props.type)} />
                </div>
            </div>    
        )
    }
}

const display = {
    block: {
        display: 'block'
    },
    none: {
        display: 'none'
    }
}

const visibility = {
    hidden: {
        visibility: 'hidden'
    },
    visible: {
        visibility: 'visible'
    }
}

const getDisplay = (isOpen) => {
    return isOpen ? display.block : display.none;
}

const getVisibility = (isOpen) => {
    return isOpen ? visibility.visible : visibility.hidden;
}

const mapStateToProps = (state, ownProps) => {
    return {
        buttonData: state.carrer.tabs.filter(x => x.selected==true)[0][ownProps.type]
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        onClickShowValues: () => {
            dispatch(carrerActions.showButtonObjects(ownProps.type));
        },
        onClickLoadValuesByParentId: (parentId, level) => {
            dispatch(carrerActions.loadButtonObjects(parentId, level + 1, ownProps.type));
        },
        onClickBackButtonValues: () => {
            dispatch(carrerActions.backButtonObjects(ownProps.type));
        }
    }
}

const ButtonComponent = CSSModules(Button, styles); 

export default connect(mapStateToProps, mapDispatchToProps)(ButtonComponent);


