import React, { PropTypes } from 'react';
import CSSModules from 'react-css-modules';
import styles from './styles.scss';

const ObjectList = ({objects, onClick}) => (
    <div styleName="ObjectList">
        <ul>
            {objects.map(o => (
                <li onClick={() => onClick(o)} key={o.id}>{o.name}</li>
            ))}
        </ul>
    </div>
)

export default CSSModules(ObjectList, styles);


